<template>
  <div class="cuerpo">
    <!-- TITULO -->
    <div class="py-6 leading-none">
      <h1 class="uppercase fw-700 text-xl">Configuración Bitácoras</h1>
    </div>

    <!-- CONTENEDOR -->
    <div
      class="
        bg-white
        rounded-t-lg
        py-4
        px-4
        flex flex-col
        md:flex-row
        md:items-end
        items-center
        justify-center
        md:justify-center
        flex-wrap
        mb-4
        md:mb-0
      "
    >
      <!-- CREADO POR -->
      <div class="md:mx-4 mx-0 w-10/12 md:w-auto mb-2 md:mb-0">
        <h1 class="font-bold text-xxs uppercase text-left">Creado por:</h1>
        <select
          class="
            bg-gris-30
            trans
            rounded-md
            px-3
            h-8
            focus:outline-none
            w-full
            md:w-auto
          "
          v-model="filtro.creadoPor"
          @change="filtrar"
        >
          <option value="TODOS">Todos</option>
          <option
            v-for="(x, y) in arrayFiltros[0].creadoPor"
            :key="y"
            :value="x"
          >
            {{ x }}
          </option>
        </select>
      </div>

      <!-- STATUS -->
      <div class="md:mr-4 mx-0 w-10/12 md:w-auto mb-2 md:mb-0">
        <h1 class="font-bold text-xxs uppercase text-left">Status:</h1>
        <select
          class="
            bg-gris-30
            trans
            rounded-md
            px-3
            h-8
            focus:outline-none
            w-full
            md:w-auto
          "
          v-model="filtro.status"
          @change="filtrar"
        >
          <option value="TODOS">Todos</option>
          <option v-for="(x, y) in arrayFiltros[0].status" :key="y" :value="x">
            {{ x }}
          </option>
        </select>
      </div>

      <!-- DESTINOS -->
      <div class="md:mr-4 mx-0 w-10/12 md:w-auto mb-2 md:mb-0">
        <h1 class="font-bold text-xxs uppercase text-left">Destinos:</h1>
        <select
          class="
            bg-gris-30
            trans
            rounded-md
            px-3
            h-8
            focus:outline-none
            w-full
            md:w-auto
          "
          v-model="filtro.destino"
          @change="filtrar"
        >
          <option value="TODOS">Todos</option>
          <option
            v-for="(x, y) in arrayFiltros[0].destinos"
            :key="y"
            :value="x"
          >
            {{ x }}
          </option>
        </select>
      </div>

      <!-- TIPO DE ACTIVADO -->
      <div class="md:mr-4 mx-0 w-10/12 md:w-auto mb-3 md:mb-0">
        <h1 class="font-bold text-xxs uppercase text-left">Tipo de activo:</h1>
        <select
          class="
            bg-gris-30
            trans
            rounded-md
            px-3
            h-8
            focus:outline-none
            w-full
            md:w-auto
          "
          v-model="filtro.tipoEquipo"
          @change="filtrar"
        >
          <option value="TODOS">Todos</option>
          <option
            v-for="(x, y) in arrayFiltros[0].tiposEquipos"
            :key="y"
            :value="x"
          >
            {{ x }}
          </option>
        </select>
      </div>

      <!-- BUSCADOR -->
      <div
        class="
          relative
          flex
          items-center
          w-full
          md:w-auto
          px-10
          md:px-0
          mb-2
          md:mb-0
          mr-0
          md:mr-4
        "
      >
        <input
          type="seach"
          class="
            bg-gris-30
            trans
            w-full
            md:w-auto
            rounded-l-md
            pl-6
            h-8
            focus:outline-none
          "
          v-model="filtro.palabra"
          placeholder="Buscar..."
          @keyup="filtrar"
        />
        <svg
          xmlns="http://www.w3.org/2000/svg"
          class="w-4 h-4 absolute top-0 mt-2 ml-1 text-gris-600"
          viewBox="0 0 24 24"
          fill="none"
          stroke="currentColor"
          stroke-width="3"
          stroke-linecap="round"
          stroke-linejoin="round"
        >
          <circle cx="11" cy="11" r="8"></circle>
          <line x1="21" y1="21" x2="16.65" y2="16.65"></line>
        </svg>
        <button
          class="
            focus:outline-none
            bg-bluedemon-200
            rounded-r-md
            h-8
            w-8
            flex
            items-center
            justify-center
          "
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="w-4 h-4 text-bluedemon-400"
            viewBox="0 0 24 24"
            fill="none"
            stroke="currentColor"
            stroke-width="3"
            stroke-linecap="round"
            stroke-linejoin="round"
          >
            <circle cx="11" cy="11" r="8"></circle>
            <line x1="21" y1="21" x2="16.65" y2="16.65"></line>
          </svg>
        </button>
      </div>

      <!-- CREAR NUEVO PLAN -->
      <div class="mx-0 w-10/12 md:w-auto">
        <button
          class="
            bg-indigo-200
            text-indigo-500
            h-8
            rounded-lg
            px-2
            text-xs
            w-10/12
            md:w-auto
          "
          @click="agregarBitacora"
        >
          Crear Plan
        </button>
      </div>
    </div>

    <!-- LISTA DE BITACORAS -->
    <div class="scrollx">
      <button class="py-3 bg-gris-30 focus:outline-none">
        <div
          class="
            flex flex-row
            items-center
            justify-evenly
            uppercase
            font-semibold
            text-xs
          "
        >
          <div class="w-12">
            <h1 class="">ID</h1>
          </div>
          <div class="w-80 flex-none">
            <h1>Creado Por</h1>
          </div>
          <div class="w-96 flex-none">
            <h1>Descripcion</h1>
          </div>
          <div class="w-20 flex-none">
            <h1>STATUS</h1>
          </div>
          <div class="w-80 flex-none">
            <h1>TIPOS</h1>
          </div>
          <div class="w-80 flex-none">
            <h1>DESTINOS</h1>
          </div>
        </div>
      </button>
      <Filabitacoras
        v-for="(x, y) in arrayListadoBitacoras"
        :key="y"
        :array="x"
        @click="pushPath(x.idBitacora)"
      />
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted } from "@vue/runtime-core";
import Filabitacoras from "../components/Filabitacoras.vue";
import { useStore } from "vuex";
import { useRouter } from "vue-router";
export default {
  components: { Filabitacoras },

  setup() {
    const store = useStore();
    const router = useRouter();

    const uniqid = require("uniqid");

    const filtro = ref({
      creadoPor: "TODOS",
      status: "TODOS",
      destino: "TODOS",
      tipoEquipo: "TODOS",
      palabra: "",
    });

    onMounted(() => {
      store.dispatch("getListadoBitacoras", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "listadoBitacoras",
      });
    });

    const arrayListadoBitacoras = computed(() => {
      return store.state.arrayListadoBitacoras;
    });

    const arrayFiltros = computed(() => {
      return store.state.arrayFiltros;
    });

    const pushPath = (id) => {
      router.push("/" + id);
    };

    const filtrar = () => {
      store.state.arrayListadoBitacoras =
        store.state.arrayListadoBitacorasFull.filter((x) => {
          if (
            (filtro.value.creadoPor.toUpperCase() ==
              x.creadoPor.toUpperCase() ||
              filtro.value.creadoPor.toUpperCase() == "TODOS") &&
            (filtro.value.status.toUpperCase() == x.status.toUpperCase() ||
              filtro.value.status.toUpperCase() == "TODOS") &&
            (x.destinos.includes(filtro.value.destino) ||
              filtro.value.destino.toUpperCase() == "TODOS") &&
            (x.tiposEquipos.includes(filtro.value.tipoEquipo.toUpperCase()) ||
              filtro.value.tipoEquipo.toUpperCase() == "TODOS") &&
            !x.descripcion
              .toUpperCase()
              .indexOf(filtro.value.palabra.toUpperCase())
          ) {
            return true;
          }
        });
    };

    const agregarBitacora = async () => {
      const idBitacora = uniqid(uniqid());

      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "agregarBitacora",
        idBitacora: idBitacora,
      });

      if (response) {
        console.log("ok");
        await pushPath(idBitacora);
      } else {
        console.log("error");
      }
    };

    return {
      arrayListadoBitacoras,
      arrayFiltros,
      pushPath,
      filtro,
      filtrar,
      agregarBitacora,
    };
  },
};
</script>

<style scoped>
.cuerpo {
  height: 100vh;
  padding-left: 10px;
  padding-right: 10px;
  overflow-x: auto;
}
.focus\:w-72:focus {
  @apply w-72;
}
.trans {
  transition: width 2s, height 2s;
}

.scrollx {
  overflow-x: scroll;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* Internet Explorer 10+ */
  scroll-margin-left: 300px;
}

.scrollx::-webkit-scrollbar {
  /* WebKit */
  width: 0;
  height: 0;
}
</style>
